// import { smsLogin } from '@/api/user'
import { getLocalStorage, setLocalStorage, removeLocalStorage } from '@/assets/js/localStorage'

const user = {
  state: {
    idNum: '',
    // token: '',
    user_name: getLocalStorage('userName'),
    token: getLocalStorage('token')
    // forgetPwdStep: sessionStorage.getItem('forgetPwdStep') ? sessionStorage.getItem('forgetPwdStep') : 1
  },
  getters: {
    token: state => state.token,
    user_name: state => state.user_name
  },
  mutations: {
    SET_IDNUM: (state, idNum) => {
      state.idNum = idNum
    },
    SET_USER_NAME: (state, name) => {
      state.user_name = name
      setLocalStorage('userName', name)
    },
    SET_TOKEN: (state, token) => {
      state.token = token
      setLocalStorage('token', token)
    }
  },
  actions: {
    Logout ({ commit, state }) {
      return new Promise((resolve, reject) => {
        commit('SET_USER_NAME', '')
        commit('SET_TOKEN', '')
        removeLocalStorage('token')
        removeLocalStorage('userName')
        resolve()
      }).catch(error => {
        reject(error)
      })
    }
  }
}

export default user
