import axios from 'axios'
import axiosConfig from './config'
/* import Message from 'components/proto/message/index.js' */
import { getAuth, checkStatus } from '@//assets/js/auth'

/**
 * 取消重复的请求
 * @param config 请求响应配置
 */

// 创建实例
const service = axios.create(axiosConfig)
// 添加请求拦截器
service.interceptors.request.use(
  (config) => {
    // 让每次请求都会带上token
    const { auth, token } = getAuth()
    if (auth && token) {
      config.headers['Authorization'] = token
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)

// 返回状态判断(添加响应拦截器)
// 添加一个返回拦截器
service.interceptors.response.use((response) => {
  try {
    checkStatus(response.data.code)
  } catch (e) {
    /* Message.error(e) */
  }
  return response
}, (error) => {
  // 对返回的错误进行一些处理
  return Promise.reject(error)
})

export default service
