import { axiosGet, axiosPost } from '../request'

// 短信登录
export const smsLogin = (params) =>
  axiosGet({
    config: { url: '/member/smsLogin', params }
  })

// 短信登陆 发送验证码
export const sendCaptchaSms = (params) =>
  axiosGet({
    config: { url: '/member/sendCaptchaSms', params }
  })

// 密码登录
export const pwdLogin = (params) =>
  axiosGet({
    config: { url: '/member/pwlogin', params }
  })

// 注册 验证身份证是否注册过
export const isIdNumRegister = (params) =>
  axiosGet({
    config: { url: '/member/register/checkIdnum', params }
  })

// 注册 验证手机号是否已经绑定
