// import { formatRequestDate } from 'utils/date';
import axios from './interceptors'

/**
 *axios 请求方法类
 *
 * @class AxiosRequest
 */
class AxiosRequest {
  constructor (config) {
    this.config = config
  }
  request () {
    return new Promise((resolve) => {
      axios(this.config)
        .then((res) => {
          const data = res.data
          resolve(data)
        })
        .catch((error) => {
          const result = error.response ? error.response.data : undefined
          resolve(result)
        })
    })
  }
}
/**
 *
 *  axios get请求
 * @param {config} config 请求配置
 * @param {boolean} joinPrefix 是否自动拼接前缀
 * @returns
 */
export function axiosGet ({ config } = {}) {
  const extend = config
  extend.method = 'GET'
  extend.data = { params: extend.params }
  return request(extend)
}

/**
 *
 *  axios post 请求
 * @param {config} config 请求配置
 * @param {boolean} joinPrefix 是否自动拼接前缀
 * @returns
 */
export function axiosPost ({
  config,
  joinParamsToUrl = false
} = {}) {
  const extend = config
  extend.method = 'POST'
  // 统一格式化请求的时间参数
  extend.data = extend.params
  extend.params = {}
  return request(extend, joinParamsToUrl)
}
/**
 * 将对象添加当作参数拼接到URL上面
 * @param baseUrl 需要拼接的url
 * @param obj 参数对象
 * @returns {string} 拼接后的对象
 * 例子:
 *  let obj = {a: '3', b: '4'}
 *  setObjToUrlParams('www.baidu.com', obj)
 *  ==>www.baidu.com?a=3&b=4
 */
function setObjToUrlParams (baseUrl, obj) {
  let parameters = ''
  let url = ''
  for (let key in obj) {
    parameters += key + '=' + obj[key] + '&'
  }
  parameters = parameters.replace(/&$/, '')
  if (/\?$/.test(baseUrl)) {
    url = baseUrl + parameters
  } else {
    url = baseUrl.replace(/\/?$/, '?') + parameters
  }
  return url
}
/**
 *
 * @param {config} extend 请求配置
 * @param {boolean} joinPrefix 是否自动拼接前缀
 */
function request (extend, joinParamsToUrl = false) {
  if (joinParamsToUrl) {
    extend.url = setObjToUrlParams(extend.url, extend.data)
    extend.data = {}
  }
  const req = new AxiosRequest(extend)
  return req.request()
}
export default { axiosGet, axiosPost }
