import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    author: 'wendycxl'
  },
  mutations: {
    MODIFY (state, msg) {
      state.author = msg
    }
  },
  modules: {
    user
  }
})

export default store
