import Vue from 'vue'
import Router from 'vue-router'

import loginRegister from './login-register'
import product from './product'
console.log(123, product)

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/37duc'
    },
    {
      path: '/37duc',
      name: '37duc',
      redirect: '/37duc/index',
      component: resolve => require(['@/views/Main.vue'], resolve),
      children: [
        {
          path: 'index',
          name: 'Index',
          component: resolve => require(['@/views/index.vue'], resolve),
          meta: {
            title: '首页'
          }
        },
        {
          path: 'user',
          name: 'User',
          redirect: '/37duc/user/smsLogin',
          component: resolve => require(['@/views/LoginRegister.vue'], resolve),
          meta: {
            title: '短信登录'
          },
          children: [
            ...loginRegister
          ]
        },
        {
          path: 'product',
          name: 'Product',
          // redirect: '/37duc/product/list',
          component: resolve => require(['@/views/index.vue'], resolve),
          meta: {
            title: '产品列表'
          },
          children: [
            ...product
          ]
        },
        {
          path: 'personCenter',
          name: 'PersonCenter',
          component: resolve => require(['@/views/personCenter.vue'], resolve),
          meta: {
            title: '个人中心'
            // requireAuth: true
          },
          children: [
            {
              path: '',
              component: resolve => require(['@/components/ThePersonCenter'], resolve),
              children: [
                {
                  path: '',
                  component: resolve => require(['@/components/ThePersonCenter/TheOrder'], resolve),
                  meta: {
                    title: '个人中心',
                    requireAuth: true
                  }
                }
              ]
            },
            {
              path: 'detail/:id',
              component: resolve => require(['@/components/ThePersonCenter/TheOrder/TheOrderDetail.vue'], resolve)
            }
          ]
        },
        {
          path: '*',
          name: 'Not-found',
          meta: {
            keepAlive: true,
            title: '模块开发中'
          },
          component: resolve => require(['@/views/NotFound.vue'], resolve)
        },
      ]
    },

    {
      path: '*',
      name: 'Not-found',
      meta: {
        keepAlive: true,
        title: '404'
      },
      component: resolve => require(['@/views/NotFound.vue'], resolve)
    }
  ]
})
