const smsLogin = resolve => require(['@/components/login/TheSmsLogin.vue'], resolve)
const pwdLogin = resolve => require(['@/components/login/ThePwdLogin.vue'], resolve)
const registerMain = resolve => require(['@/components/register/TheRegisterMain.vue'], resolve)
const register = resolve => require(['@/components/register/TheRegister.vue'], resolve)
const registerSucceed = resolve => require(['@/components/register/TheRegisterSucceed.vue'], resolve)
const forgetPassword = resolve => require(['@/components/password/TheForgetPassword.vue'], resolve)
const mobileValid = resolve => require(['@/components/password/TheMobileValid'], resolve)
const identifyValid = resolve => require(['@/components/password/TheIdentifyValid'], resolve)
const resetPassword = resolve => require(['@/components/password/TheResetPassword'], resolve)
const resetPasswrodSucceed = resolve => require(['@/components/password/TheResetPasswordSucceed'], resolve)
const setPassword = resolve => require(['@/components/password/TheSetPassword'], resolve)
const setPasswordMain = resolve => require(['@/components/password/TheSetPasswordMain'], resolve)
const setPasswordSucceed = resolve => require(['@/components/password/TheSetPasswordSucceed'], resolve)
export default [
  {
    path: 'smsLogin',
    name: 'SmsLogin',
    component: smsLogin,
    meta: {
      title: '短信登录'
    }
  },
  {
    path: 'pwdLogin',
    name: 'PwdLogin',
    component: pwdLogin,
    meta: {
      title: '密码登录'
    }
  },
  {
    path: 'register',
    name: 'RegisterMain',
    redirect: 'register/main',
    component: registerMain,

    children: [
      {
        path: 'main',
        component: register,
        meta: {
          title: '注册'
        }
      },
      {
        path: 'succeed',
        component: registerSucceed
      }
    ]
  },
  {
    path: 'forgetPassword',
    redirect: 'forgetPassword/mobileValid',
    name: 'ForgetPassword',
    component: forgetPassword,
    children: [
      {
        path: 'mobileValid',
        component: mobileValid,
        meta: {
          title: '忘记密码'
        }

      },
      {
        path: 'identifyValid',
        name: 'IdentifyValid',
        component: identifyValid,
        meta: {
          title: '忘记密码'
        }
      },
      {
        path: 'resetPassword',
        component: resetPassword,
        meta: {
          title: '忘记密码'
        }
      },
      {
        path: 'succeed',
        component: resetPasswrodSucceed,
        meta: {
          title: '忘记密码'
        }
      }
    ]
  },
  {
    path: 'setPassword',
    name: 'SetPasswordMain',
    redirect: 'setPassword/main',
    component: setPasswordMain,
    meta: {
      title: '设置密码',
      requireAuth: true
    },
    children: [
      {
        path: 'main',
        component: setPassword,
        meta: {
          title: '设置密码',
          requireAuth: true
        }
      },
      {
        path: 'succeed',
        component: setPasswordSucceed,
        meta: {
          title: '设置密码',
          requireAuth: true
        }
      }
    ]
  }
]
