import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import './assets/styles/global.scss'
import Api from '@/api/index.js'

import moment from 'moment'
Vue.use(Api)

Vue.filter('datefmt', (input, fmtstring = 'YYYY-MM-DD HH:mm:ss') => {
  return moment(input).format(fmtstring)
})

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title + '_37度C健康保险网'
  }
  if (to.meta.requireAuth) {
    console.log('main', to, store.state.user.token)
    if (store.state.user.token) { // 通过vuex state获取当前的token是否存在
      next()
    } else {
      next({
        path: '/37duc/user/smsLogin'
      })
    }
  } else {
    next()
  }
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
