const fetchApiMethods = require.context('./modules/', true, /\.js$/)

const modules = {}
fetchApiMethods.keys().forEach((key) => {
  if (key) {
    const module = fetchApiMethods(key)
    key = key.replace(/^\.\//, '').replace(/\.js$/, '')
    modules[key] = { ...module }
  }
})
const install = (vue) => {
  vue.prototype.$api = modules
}
export default {
  install
}
